<template>
	<b-container :class="$style.relativeContainer">
		<transition name="slide-tab" mode="out-in">
			<component :is="currentTabComponent"></component>
		</transition>
		<transition appear name="fade">
			<app-tab-row v-show="showTabRow" @changeTab="setActualTab($event)" :actualTab="actualTab" :class="$style.stickyMenu"/>
		</transition>
	</b-container>
</template>
<script>
import TabRow from '@/components/tabs/TabRow.vue'
import Initial from '@/components/tabs/Initial.vue'
import Education from '@/components/tabs/Education.vue'
import Skills from '@/components/tabs/Skills.vue'
import Experiences from '@/components/tabs/Experiences.vue'

import { EventBus } from '@/event-bus.js'

export default {
	name: 'About',
	data () {
		return {
			lastScroll: 0,
			showTabRow: true,
			actualTab: 'Initial'
		}
	},
	components: {
		'app-tab-row': TabRow,
		'app-initial': Initial,
		'app-education': Education,
		'app-experiences': Experiences,
		'app-skills': Skills
	},
	mounted () {
		// window.addEventListener('scroll', this.scrollDirection)
		EventBus.$on('resetWork', this.goHome)
	},
	computed: {
		currentTabComponent () {
			return 'app-' + this.actualTab.toLowerCase()
		}
	},
	methods: {
		setActualTab (event) {
			this.actualTab = event.target.textContent
		},
		// scrollDirection () {
		// 	const st = window.pageYOffset || document.documentElement.scrollTop
		// 	this.showTabRow = !(st > this.lastScroll)
		// 	this.lastScroll = st
		// },
		goHome () {
			this.actualTab = 'Initial'
		}
	}
}
</script>
<style lang="scss" module>
.relativeContainer {
	position: relative;
	flex: 1 0 100%;
	padding-bottom: 40px;

	@include biggerThan($screen-sm) {
		display: flex;
		align-items: center;
	}

}

.stickyMenu {
	position: fixed;
	width:100%;
	bottom:0;
	left:0;
	display: flex;
	background-color: $main-background;
}
</style>



