<template>
	<div>
		<b-row>
			<b-col cols="12">
				<app-grid-container :data="experiences" type="cx" colForRow="12" colForRowLg="9" />
			</b-col>
		</b-row>
	</div>
</template>
<script>
import experiences from '@/data/experiences.js'
import GridContainer from '@/components/grids/GridContainer.vue'

export default {
	name: 'Experiences',
	data () {
		return {
			experiences: experiences
		}
	},
	components: {
		'app-grid-container': GridContainer
	}
}
</script>



