<template>
	<b-container>
		<b-row :class="$style.titleRow" align-v="center">
			<b-col offset="1" offset-md="0" cols="10">
				<h1 class="title home">Dino Pascale</h1>
				<h2 class="subtitle">
					Front-end developer.
				</h2>
			</b-col>
		</b-row>
		<b-row :class="$style.workRow">
			<b-col offset="1" offset-md="0" cols="12" md="8">
				<app-gridwork></app-gridwork>
			</b-col>
			<b-col cols="4" :class="['d-none d-lg-flex', $style.imgRow]">
					<img :src="img"/>
					<p><a :class="['app-link', $style.imgSource]" href="https://media.giphy.com/media/E4cBYXIciRhhC/giphy.gif" target="_blank">Source</a></p>
			</b-col>
		</b-row>
	</b-container>
</template>
<script>
import GridWork from '@/components/grids/works/GridWork.vue'
import img from '@/assets/img/dinosaur.gif'

export default {
	name: 'home',
	data () {
		return {
			img: img
		}
	},
	components: {
		'app-gridwork': GridWork
	}
}
</script>
<style module lang="scss">
	.titleRow {
		height: 30vh;

		@include biggerThan($screen-md) {
			height: 35vh;
		}
	}

	.workRow {
		padding-top: 40px;
	}

	.imgRow {
		justify-content: center;
		transform: translateY(-120px);
		position: relative;
	}

	.imgSource {
		position: absolute;
		bottom:-10px;
		right:0;
		width: 100%;
		transform-origin: bottom top;
		// transform: rotate(90deg) translate(-50%, -0%);
		text-align: center;
		font-size: 12px;
	}
</style>



