<template>
	<b-container :class="$style.fullContainer" >
		<b-row align-h="center" :class="$style.greetImgContainer">
			<b-col cols="10">
				<img :class="$style.greetImg" :src="img" />
				<p><a :class="['app-link', $style.greetSource]" href="https://giphy.com/stickers/bird-z0Hygtrs7M6WI" target="_blank">Source</a></p>
			</b-col>
		</b-row>
		<b-row align-h="center" align-v="center" :class="$style.greetImgContainer">
			<b-col cols="5" v-for="(contact, i) in social" :key="i">
				<app-contact-row :contact="contact" />
			</b-col>
		</b-row>
	</b-container>
</template>
<script>
import contactRow from '@/components/contact/contactRow'
import social from '@/data/social.js'
import img from '@/assets/img/contact.gif'

export default {
	name: 'contacts',
	data () {
		return {
			social: social,
			img: img
		}
	},
	components: {
		'app-contact-row': contactRow
	}
}
</script>
<style lang="scss" module>
	.fullContainer {
		flex: 1 0 100%;
		padding-bottom: 30px;
		display: flex;
		flex-flow:column;
	}

	.greetImgContainer {
		flex: 1;
		text-align: center;
	}

	.greetImg {
		width:100%;
		height:auto;
		margin: 0 auto;
		max-width: 500px;
		padding-bottom: 40px;
	}

	.greetSource {
		font-size: 12px;
	}
</style>



