<template>
	<b-row :class="$style.gridContainer" align-v="start" :align-h="colForRowLg !== '6' && colForRow === '12' ? 'center' : null">
		<b-col cols="12" v-if="header">
			<h2 :class="$style.titleContainer">{{header}}</h2>
		</b-col>
		<app-grid-item v-for="(item, i) in data" :key="i" :el="item" :colForRow="colForRow" :colForRowLg="colForRowLg" :type="type" :centered="centered"/>
	</b-row>
</template>
<script>
import GridItem from '@/components/grids/GridItem.vue'

export default {
	name: 'GridContainer',
	props: {
		data: { default: function () { return {} } },
		colForRow: { default: '12' },
		colForRowLg: { default: '6' },
		header: { default: null },
		type: { default: null },
		centered: { default: true }
	},
	components: {
		'app-grid-item': GridItem
	}
}
</script>
<style lang="scss" module>
.gridContainer {
	padding-bottom: 30px;
}

.titleContainer {
	padding-top:20px;
	padding-bottom: 20px;
	text-align: center;
	font-size: $font-small;
	letter-spacing: 5px;
	font-family: $font-text;
	text-transform: uppercase;
}
</style>



