<template>
	<div :class="$style.contactContainer">
		<a :href="contact.link" :class="['app-link', $style.contactItem]" :target="contact.target ? '_blank' : '_self'">{{contact.name}}</a>
	</div>
</template>
<script>
export default {
	name: 'contactRow',
	props: {
		contact: { default: function () { return { } } }
	}
}
</script>
<style lang="scss" module>
	.contactItem {
		font-size: 18px;
		text-decoration: underline;

		@include biggerThan($screen-sm) {
			font-size: $font-xsmall;
		}
	}

	.contactContainer {
		padding: 10px 0;
		position: relative;
	}
</style>



