<template>
	<b-container>
		<b-row align-h="center">
			<b-col cols="9" :class="$style.imgContainer">
				<img :src="img" :class="$style.img" />
			</b-col>
			<b-col cols=11>
				<h2 :class="$style.mexTitle">I can't find the page you're looking for!</h2>
				<p :class="$style.mexContent"><router-link to="/" :class="['app-link']">Go back to Home</router-link></p>
			</b-col>
		</b-row>
	</b-container>
</template>
<script>
import error from '@/assets/img/not_found.png'

export default {
	data () {
		return {
			img: error
		}
	}
}
</script>
<style lang="scss" module>
	.imgContainer {
		padding-top: 40px;
		text-align:center;

		@include biggerThan($screen-md) {
			padding-top: 60px;
		}
	}

	.img {
		width: 100%;
		max-width:400px;
		margin:0 auto;
	}
	.mexTitle, .mexContent {
		text-align: center;
		font-family: $font-text;
		line-height: 1.6;
	}

	.mexTitle {
		padding-top: 40px;
		font-size: $font-xsmall;
		text-transform:uppercase;

		@include biggerThan($screen-md) {
			padding-top:60px;
			font-size: $font-small;
		}
	}

	.mexContent {
		padding-top:20px;
		font-size: $font-xxsmall;
		text-decoration: underline;

		@include biggerThan($screen-md) {
			padding-top:40px;
			font-size: $font-xsmall;
		}
	}

</style>



