<template>
	<b-col :cols="colForRow" :lg="colForRowLg" :class="[$style.gridItem, centered ? $style.centered : $style.lefted]">
		<transition name="fade" mode="out-in" v-if="el.img">
			<img :class="colForRow === '12' ? $style.imgItem : $style.imgItems" v-lazy="img" />
			<!-- <div v-else :class="$style.placeholder"></div> -->
		</transition>
		<h4 v-if="el.interval" :class="$style.intervalItem">{{el.interval}}</h4>
		<h3 :class="colForRow === '12' || type === 'exp' ? $style.titleItem : $style.titleItems">{{el.name}}</h3>
		<h4 v-if="el.pos" :class="$style.posItem">{{el.pos}}</h4>
		<p v-if="el.descr" :class="$style.descrItem">{{el.descr}}</p>
		<!-- <template v-if="el.strong">
			<p :class="$style.knwoledgeItem">{{el.strong}}</p>
		</template> -->
	</b-col>
</template>
<script>
import noImg from '@/assets/img/no_img.png'

export default {
	name: 'gridItem',
	data () {
		return {
			img: noImg,
			loading: true
		}
	},
	mounted () {
		this.$nextTick(() => {
			if (this.el.img) this.getPath()
		})
	},
	props: {
		el: { default: function () { return {} } },
		colForRow: { default: '12' },
		colForRowLg: { default: '6' },
		type: { default: null },
		centered: { default: true }
	},
	methods: {
		getPath () {
			this.toSrc()
				.then(src => { this.loading = false; this.$nextTick(() => { this.img = src }) })
				.catch(err => { console.log(err); this.img = './' })
		}
	},
	computed: {
		toSrc () {
			return () => import(`@/assets/img/${this.el.img}`)
		},
		numCols () {
			return 12 / this.colForRow
		}
	}
}
</script>
<style lang="scss" module>
	.gridItem {
		margin: 30px 0;
		text-align: center;
	}

	.centered {
		text-align: center !important;
	}

	.lefted {
		@include biggerThan($screen-sm) {
			text-align: left !important;
		}
	}

	.imgItem {
		width:100px;
		height: auto;
		margin: 0 auto;
	}

	.imgItems {
		max-width:100%;
		height: 60px;
		width: auto;
	}

	.placeholder {
		width: 100px;
		height:112px;
		margin: 0 auto;
	}

	.titleItem {
		padding-top: 15px;
		font-size: $font-xsmall;
		font-family: $font-text;
		line-height: 1.6;

		@include biggerThan ($screen-sm) {
			text-transform: uppercase;
		}

	}

	.titleItems {
		padding-top: 20px;
		font-size: $font-xxsmall;
		font-family: $font-text;
		line-height: 1.3;
	}

	.intervalItem {
		font-size: 16px;
		font-family: $font-text;
		opacity: 0.7;
	}

	.posItem {
		padding: 15px 0;
		font-style: italic;
		font-size: 14px;
		line-height: 1.6;
		font-family: $font-text;
	}

	.descrItem {
		padding-left: 20px;
		padding-right: 20px;
		padding-top: 10px;
		font-size: 14px;
		font-family: $font-text;
		text-align: justify;
		line-height: 1.6;

		@include biggerThan ($screen-sm) {
			font-size:17px;
			line-height: 1.8;
			padding-top: 20px;
		}
	}

	.knwoledgeItem {
		font-family: $font-text;
		font-size:12px;
		font-style: italic;
	}
</style>



