<template>
	<div
	:class="$style.tabItem"
	@click="$emit('changeTab',$event)">
		<p :class="[$style.tabTitle, actual ? $style.active : '']">{{title}}</p>
	</div>
</template>
<script>
export default {
	name: 'tabItem',
	props: {
		cols: { default: 12 },
		title: { default: '' },
		actual: { default: false }
	}
}
</script>
<style lang="scss" module>
	.tabItem {
		flex: 1;
		padding: 20px 10px;
		transition: all .4s ease-in-out;
		cursor: pointer;
	}
	.tabTitle {
		text-align: center;
		font-family: $font-text;
		font-size: 14px;
		margin-bottom: 0;
		opacity: .9;
	}

	.active {
		opacity: 1;
		color: $primary;
		font-weight: bold;
	}
</style>



