<template>
	<div>
		<app-tab-item v-for="tab in tabs" :key="tab"
		:class="$style.tabRow"
		:actual="tab === actualTab"
		:title="tab"
		@changeTab="setActual($event)"/>
	</div>
</template>
<script>
import texts from '@/data/texts.js'
import TabItem from '@/components/tabs/TabItem.vue'

export default {
	name: 'tabRow',
	data () {
		return {
			tabs: texts.tabs
		}
	},
	props: {
		actualTab: { default: '' }
	},
	components: {
		'app-tab-item': TabItem
	},
	computed: {
		numberCols () {
			return 12 / texts.tabs.length
		}
	},
	methods: {
		setActual (event) {
			this.$emit('changeTab', event)
		}
	}
}
</script>
<style lang="scss" module>
	.tabRow {
		// border-top: 1px solid #444;
	}
</style>



