<template>
	<div>
		<!-- <b-row :class="$style.rowLogo" align-v="center">
			<b-col cols="12">
				<img :src="image" :class="$style.aboutLogo" />
			</b-col>
		</b-row> -->
		<b-row align-h="center">
			<b-col cols="11" md="9" lg="8">
				<h2 :class="$style.briefDescr">Born and raised in Italy, I like programming and creating useful "web things" that improve people's business</h2>
			</b-col>
		</b-row>
	</div>
</template>
<script>
import image from '@/assets/red-ast.png'

export default {
	name: 'Initial',
	data () {
		return {
			image: image
		}
	}
}
</script>
<style lang="scss" module>
.rowLogo {
	text-align: center;
	height: 35vh;

	@include biggerThan($screen-md) {
		height: 35vh;
	}
}

.aboutLogo {
	width:150px;
	height:auto;
	transform: rotate(90deg)
}

// .aboutLogo {
// 	font-size: $font-xxlarge;
// 	line-height: 100px;
// }

.briefDescr {
	padding-top: 100px;
	font-size: $font-xsmall;
	font-family: $font-text;
	line-height: 1.5;

	@include biggerThan($screen-sm) {
		padding-top:0px;
		font-size: $font-small;
		line-height: 1.8;
	}
}
</style>



