<template>
	<b-container :class="$style.navbar" fluid>
		<b-row align-h="end">
			<b-col cols="4" md="auto" v-for="(link, i) in menuLink" :key="i"

				:class="[isActive(link) ? $style.activeLink : $style.notActiveLink, $style.navbarItem]"
				@click="link.name === 'about' ? resetWork($event) : ''">
				<router-link :to="link.path" class="app-link nav-link">{{link.name}}</router-link>
			</b-col>
		</b-row>
	</b-container>
</template>
<script>
import { EventBus } from '@/event-bus.js'

export default {
	name: 'navbar',
	data () {
		return {
			menuLink: []
		}
	},
	mounted () {
		this.$nextTick(() => {
			this.menuLink = this.$router.options.routes.map(r => { return { name: r.name, path: r.path } }).filter(r => r.name !== 'single' && r.name !== 'notFound')
		})
	},
	methods: {
		isActive (link) {
			return this.$route.name === link.name
		},
		resetWork (event) {
			EventBus.$emit('resetWork')
		}
	}
}
</script>
<style lang='scss' module>
.navbar {
	padding: 5px 20px;
}

.navbarItem {
	font-size: 15px;
	text-align:center;

	@include smaller {
		font-size: 14px;
	}
}

.activeLink {
	font-weight: bold;
}
.notActiveLink {
	opacity: .5;
}
</style>



