<template>
	<div>
		<b-row>
			<b-col cols="12">
				<b-row v-b-toggle.tech :class="$style.toggleRow" align-h="center">
					<b-col cols="10">Tech</b-col>
				</b-row>
				<b-collapse visible id="tech" :class="$style.collapseContent">
					<app-grid-container v-b-toggle.tech :data="skills.tech" colForRow="6" colForRowLg="3" />
				</b-collapse>
				<b-row v-b-toggle.soft :class="$style.toggleRow" align-h="center">
					<b-col cols="10">Soft</b-col>
				</b-row>
				<b-collapse visible id="soft">
					<app-grid-container v-b-toggle.soft :data="skills.soft" colForRow="12" colForRowLg="6" />
				</b-collapse>
				<!-- <app-accordion :numItem="2">
					<h3 slot="title1">Tech</h3>
					<app-grid-container slot="content1" :data="skills.tech" colForRow="2" />
					<h3 slot="title2">Soft</h3>
					<app-grid-container slot="content2" :data="skills.soft" colForRow="1" />
				</app-accordion> -->

			</b-col>
		</b-row>
	</div>
</template>
<script>
import skills from '@/data/skills.js'
import GridContainer from '@/components/grids/GridContainer.vue'
import bCollapse from 'bootstrap-vue/es/components/collapse/collapse'
import vBToggle from 'bootstrap-vue/es/directives/toggle/toggle'

export default {
	name: 'Skills',
	data () {
		return {
			skills: skills
		}
	},
	directives: {
		'b-toggle': vBToggle
	},
	components: {
		'app-grid-container': GridContainer,
		'b-collapse': bCollapse
	}
}
</script>
<style lang="scss" module>

	.toggleRow {
		font-family: $font-text;
		font-size: $font-small;
		text-align: center;
		padding-bottom:5px;
		letter-spacing: 10px;
		text-transform: uppercase;
		cursor:pointer;
	}
	.collapseContent {
		// padding-bottom: 30px;

	}
</style>



