<template>
	<app-gridworkelement :works="works"></app-gridworkelement>
</template>
<script>
import works from '@/data/works'
import GridWorksElement from '@/components/grids/works/GridWorkElement.vue'

export default {
	name: 'GridWorks',
	components: {
		'app-gridworkelement': GridWorksElement
	},
	data () {
		return {
			works: {}
		}
	},
	mounted () {
		this.$nextTick(() => {
			this.works = works
		})
	}
}
</script>
<style lang="scss" module>

</style>



