<template>
	<b-row>
		<b-col v-for="(work, key, i) in works" :key="i" cols="12" :class="$style.singlegrid">
			<router-link :to="'/works/' + key" class="app-link">{{'0' + (i+1) + '.'}}{{work.name}}</router-link>
		</b-col>
	</b-row>
</template>
<script>
export default {
	name: 'GridWorkElement',
	props: {
		works: { default: function () { return {} } }
	}
}
</script>
<style lang="scss" module>
.singlegrid {
	height: 75px;
	font-family: $font-text;
	font-size: $font-xxsmall;
	text-decoration: underline;

	@include biggerThan($screen-md) {
		font-size: $font-xsmall;
		height:100px;
	}
}
</style>



