<template>
	<div>
		<b-row align-h="center">
			<b-col cols="12">
				<app-grid-container :data="education" colForRowLg="8"/>
			</b-col>
		</b-row>
	</div>
</template>
<script>
import education from '@/data/education.js'
import GridContainer from '@/components/grids/GridContainer.vue'

export default {
	name: 'Education',
	data () {
		return {
			education: education
		}
	},
	components: {
		'app-grid-container': GridContainer
	}
}
</script>



