import skills from '@/data/skills.js'

const works = {
	myVisto: {
		name: 'My Visto',
		link: 'https://myvisto.it/',
		description: `
			<p>MyVisto is a web portal which connects companies with video producers. I worked on this project as front-end dev junior in StarBoost s.r.l.'s team.
			My job was to create reusable components which was used to build different pages of the website, according to UX/UI team's indications.
			Furthermore I worked to connect the webapp with the back-end API and the Wordpress API (for the Blog section). Overall this project taught me a lot of new things: working with a team of developer with Git, a new Javascript Library (Vue.js) and a new CSS Framework (Bulma)</p>
		`,
		techs: [skills.tech.VueVuex, skills.tech.Bulma, skills.tech.Nuxt, skills.tech.Webpack]
	},
	mtgDiscovery: {
		name: 'MtG Discovery',
		link: 'http://dinopascale.altervista.org/mtgdiscovery2/',
		description: `
			<p>MtG Discovery is an event locator for the Italian Magic: The Gathering tournaments. It's my first big project and it's born as a way to test myself.
			In fact, the whole site is built with only Javascript, without any framework or library. With MtG Discovery I focused on the basis of Javascript and
			the architecture of a webapp and so I approached topics like MVC Model, Observer pattern, Functional Programming, etc. I took the opportunity also for
			try some new features of ES6 like classes. You can find, if you know Italian, some more information on <a target="_blank" href="https://github.com/dinopascale/Discovery">project's repo</a></p>
		`,
		techs: [skills.tech.HTML5, skills.tech.CSS3, skills.tech.Javascript, skills.tech.Webpack]
	},
	primitive: {
		name: 'Primitive Bontà',
		link: 'http://www.primitivebontasrl.it/',
		description: `
		<p>
			Primitive Bontà is a company based in Italy that deals with wholesale sales of frozen products. As my second job as a web developer freelancer and
			following the customer's needs, we decided to use Wordpress for building the website from zero. I designed and wireframed the whole website, wrote 
			all texts following SEO best practices and tried to optimize Wordpress for optimal load speed.	
		</p>		
		`,
		techs: [skills.tech.Wordpress, skills.tech.HTML5, skills.tech.CSS3]
	},
	santelena: {
		name: 'Sant\'Elena Service',
		link: 'http://www.santelenaservice.it/',
		description: `
		<p>
			Sant'Elena Service is a company based in Italy that deals with industrial machinery for the catering and pastry industry. This was my first work as a freelance web designer
			and following the customer's needs, we decided to use Wordpress as CMS. This job introduced me to the web development world: hosting and webmail services, SEO strategy,
			wireframing and designing a website from zero and the "wonderful" land of Wordpress. Overall Sant'Elena Service gave me the opportuinity to use my knoweldges on a real website
		</p>
		`,
		techs: [skills.tech.Wordpress, skills.tech.HTML5, skills.tech.CSS3]
	}
}

export default works



